import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

import { PresentationCollateral } from '@backbase/data-ang/loans';

import { LoanItemCustomBase } from '../loan-info.model';
import { Loan } from '../../models/loan.model';

@Component({
  selector: 'bb-loan-info-collateral-section',
  templateUrl: './loan-info-collateral-section.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanInfoCollateralSectionComponent implements LoanItemCustomBase {
  /**
   * Loan item
   */
  @Input() set loan(loan: Loan) {
    this.collaterals = loan.collateral?.filter((item) => Object.values(item).some((val) => val));
  }

  /**
   * Collection of filtered loan collaterals
   */
  collaterals: PresentationCollateral[] | undefined;
}
